.header {
    grid-area: header;
    background-color: rgb(231, 168, 203);
    padding: 10px;
    display: grid;
    grid-template-columns: 1fr 7fr 1fr ;
    
}
.header h1{
    letter-spacing: 0.02em;
    font-family: "Open Sans",
    sans-serif;
    font-weight: 300;
    text-align: center;
    line-height: 1.5em;
    margin: 0;
}
.logo{
    width: 50px;
    border-radius: 40px;
}
.loginBlock{
display: inline-flex;
}
.mylogo{
    float: right;
    width: 50px;
    border-radius: 40px;
    margin-top: -5px;
}
.login{
    padding-right: 10px;
    padding-top: -10px;
}
