.nav {
    grid-area: nav;
    background-color: rgb(231, 168, 203);
    padding: 10px;
}
.navLink {
    color: rgb(250, 235, 217);
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 0.1em;
    transition: all 0.5s ease;
}
.linkHover:hover {
    color: rgb(126, 119, 119);
}