.navLink {
    font-weight: bold;
    color: rgb(8, 8, 8);
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 0.1em;
    transition: all 0.5s ease;
}

.linkHover:hover {
    color: rgb(209, 51, 51);
}

.LogoImage {
    width: 90px;
    border-radius: 30px;
}

.userslist {
    display: flex;
    padding: 5px;
}

.userInfo {
    border: 1px;
    border-radius: 5px;
}

.userMessage {
    padding-left: 10px;
    background-color: rgb(253, 253, 253);
    border: 1px;
    border-color: rgb(221, 68, 157);
    border-radius: 5px;

}

.selectedPage {
    font-weight: bold;

}

.pages {
    overflow: hidden;
    text-overflow: ellipsis;
}

.preloader {
    width: 400px;
    display: block;
    margin-left: auto;
    margin-right: auto
}